.mainBox{
    width: fit-content;
    min-width: 300px;
    margin: auto;
    padding: 20px;
    border: none;
    background: #424242;
    height: 400px;
    overflow: auto;
    padding-top: 0;

}
.mainBox > h6{
    font-weight: 100;
    color: rgba(255, 255, 255, 0.555);
    border-bottom: 1px solid teal;
    width: 70%;
    margin: 0;
    padding-top: 10px;
}