.listBox{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    margin: 5px 0;
    border-bottom: 1px solid rgba(0, 128, 128, 0.336);
    border-left: 1px solid rgb(0, 128, 128);
    box-shadow: -1px 2px 2px rgb(0, 128, 128);
}
.listBox img {
    width: 15px;
    height: 15px;
    cursor: pointer;
}
.title{
    flex-grow: 1;
}
.listBox>*{
margin: auto 10px;
}
.listBox button {
background-color: transparent;
border: none;
cursor: pointer;
}
.listBox input{
   color: transparent;
}