.mainBox{
    position: relative;
    background: #40257A;
    margin-top: 100px;
    box-shadow: 3px 3px 30px 20px #0b242b;

}
.Head{
    background-color: #5E35B1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
    padding:5px 15px;
}
.Head>h1{
    margin: 0;
    color: white;
    font-weight: 400;
    text-decoration: underline;
    font-size: 30px;
}
.Head label{
    color: rgb(3, 185, 185);
    font-size: 13px;
    font-weight: 500;
}