.inputBox{
    position: absolute;
    bottom:20px;
    right: 20px;
    background: #1c54b2;
    padding: 10px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.562);
    transition: all 300ms ease-in-out;
}
.inputBox:hover{
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.651);
    background: #1f5bc2;
}
.inputBox>input ,.inputBox>button{
    background-color: transparent;
    border: none;
    padding:0 10px;
}
.inputBox>input:focus ,.inputBox>button:focus{
    outline: none;
}
.inputBox>button{
    font-size: 30px;
}
.inputBox>input::placeholder{
    color: rgba(255, 255, 255, 0.616);

}
.inputBox>*{
    color: white;
}
